.modal-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ======= MODAL ======= */
.modal-overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  opacity: 0;
  visibility: hidden;
  z-index: 9999;
}

.modal {
  background-color: #F0F2F5;
  padding: 2.4rem;
  position: relative;
  z-index: 1;
}

.modal-overlay.active {
  opacity: 1;
  visibility: visible;
}